.form {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translateY(-50%) translateX(50%);
  width: 400px;
  height: 620px;
  background-color: #EFEFEF;
  border-radius: 50px 50px 50px 50px;
  box-shadow: 5px 5px 50px 5px rgba(0, 0, 0, 0.10);
  border: 2px dotted orangered;
}

.registration__form {
  color: #000;
  margin: 60px 25px 40px 25px;
}

.container__create {
  display: block;
}


.create {
  margin: 10px 0 0 60px;
  width: 250px;
  height: 55px;
  border-radius: 20px;
  color: white;
  font-family: Cocomat, sans-serif;
  font-size: 21px;
  background: linear-gradient(162deg, #FCA311 0%, #D15 100%);
}

@media (max-width: 400px) {
  .form {
    width: 350px;
  }
  .create {
    margin-left: 25px;
  }
}



