.container {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(to right, rgb(42,3,48), rgb(20,1,46));
}

.mainContainer {
  opacity: 0;
  transform: translate(0px, 20%);
  display: flex;
  width: 1200px;
  height: calc(100vh - 75px);
  align-items: self-start;
  justify-content: center;
  z-index: 10;
  position: absolute;
  margin-top: 75px;
  margin-left: 60px;
  color: white;
  flex-direction: column;
  transition: all 1s ease;

  .days {
    width: 100%;
    font-size: 20px;
    margin: 10px 10px 20px 10px;
    h3 {
      padding: 5px;
    }
  }
}




