@import './font.css';
body {
    font-family: Cocomat, sans-serif;
    color: #fff;
}

*{
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
}


.background {
    padding-top: 75px;
    width: 100%;
    height: 600px;
    background-image: url("../static/photo/backgroung/bg5.jpg");
    background-attachment: fixed;
    background-size: cover;
    background-repeat: no-repeat;
}

.registration {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}