.Container {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: rgba(0,0,0,.2);
}

.Header {
  color: white;
  font-size: 3em;
  line-height: 75px;
  text-shadow: 3px 5px 5px black;
  cursor: pointer;
}


.timer {
  position: absolute;
  top: 20px;
  right: 200px;
  color: white;
  text-shadow: 3px 5px 5px black;
  font-size: 25px;
  transition: all 0.2s ease;
}
